$bg: #1e1b18;
$secondColor: #b4b4b4;
$textColor: #f4fec1;
$fontTitle: "Edensor", sans-serif;
$fontText: "Champagne & Limousines", sans-serif;
$fontNumber: "Altone Trial", sans-serif;
$fontName: "against", sans-serif;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Visuelt Pro", sans-serif;
}

body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    scroll-behavior: smooth;

    background: $bg;
    background-image: linear-gradient(rgba(255, 255, 255, 0.05) 0.1em, transparent 0.1em),
        linear-gradient(90deg, rgba(255, 255, 255, 0.05) 0.1em, transparent 0.1em);
    background-size: calc(10vw / 2.5) calc(10vw / 2.5);
}
