@import "../styles/index.scss";

.container-project {
  margin-top: 5vh;
  padding: 25vh;
  padding-top: 0;
  padding-bottom: 0;
  z-index: 10;
  color: white;
  font-family: "Visuelt Pro", sans-serif;
  font-size: 1.2vw;
  font-weight: 400;
}

.title-project {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1vh;
  text-transform: uppercase;
  font-family: "Aubrey", sans-serif;
}

.description-project {
  margin-bottom: 5vh;
}

.directional-link {
  margin-top: 5vh;
  color: white;
  text-decoration: underline;
  display: flex;
  align-items: center;
  gap: 5px;
}
.makeby {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 5vh;
}

.makeby p {
  font-weight: 500;
  font-style: italic;
}

.presentation {
  width: -webkit-fill-available;
  margin-top: 3vh;
  margin-bottom: 3vh;
  box-shadow: 0 0 20px 2px rgba(217, 217, 217, 0.2);
}

.container-img {
  display: flex;
  align-items: baseline;
  width: max-content;
}

.container-img img {
  width: 300px;
  height: 100%;
  object-fit: cover;
}

.solologo {
  width: 200px;
}

.link {
  text-decoration: underline;
  color: black;
}

.mobile-img {
  width: 20vw;
}

.top-1 {
  margin-top: 5vh;
}

.link-about {
  color: black;
  border-bottom: 2px solid #000;
  text-decoration: none;
  display: flex;
  gap: 10px;
  align-items: center;
  width: fit-content;
  font-size: 1.5rem;
}
