@media screen and (max-width: 1200px) {
    .App {
        .home {
            width: 100%;
        }

        .title {
            font-size: 7vw !important;
            top: 50%;
        }

        .ds-de {
            font-size: 2rem;
            left: 50%;
            transform: translate(-50%, -50%);
            width: max-content;
            top: 57%;
        }

        .welcom {
            font-size: 1rem !important;
            width: max-content;
            top: 60%;
        }

        .arrow-home {
            width: 3rem;
        }

        .ver {
            display: none;
        }

        .container-number {
            margin-top: 0 !important;
            gap: 3vw !important;
            svg {
                width: 10vw;
            }
        }

        .description-section {
            margin-left: 0 !important;
            font-size: 5vw !important;
        }

        .guide-line {
            display: none;
        }

        .container-projects {
            width: max-content;
            margin: auto;
            margin-top: 10vw !important;
            margin-left: auto !important;
        }

        .NavLink {
            margin-top: 15vw !important;

            &:first-child {
                margin-top: 0 !important;
            }
        }

        section {
            margin-top: 20vw !important;
            &:nth-child(1) {
                margin-top: 0 !important;
            }
        }

        .project {
            .project-img {
                width: 60vw !important;
            }

            .project-title {
                font-size: 5vw !important;
            }
        }
    }

    .container-project {
        .title-project {
            font-size: 8.5vw;
        }
        padding: 8.5vw !important;

        font-size: 3vw !important;

        .mobile-img {
            width: 90vw !important;
        }

        .font-used {
            margin-top: 5vh !important;
        }

        .makeby {
            flex-direction: column;
            align-items: left !important;
        }
    }
    .Nav .container-choices-lang a,
    p {
        font-size: 1rem !important;
    }

    .container-about section {
        margin: 9vw !important;
        &:nth-child(1) {
            margin-top: 15vh !important;
        }

        .description-section {
            width: 100%;
            font-size: 5vw !important;
        }
    }

    .link {
        color: white !important;
    }
}
