@import "../styles/index.scss";

.App {
    .cursor {
        border-radius: 50%;
        background-color: white;
        position: absolute;
        z-index: 200;
        mix-blend-mode: difference;
    }

    .ds-de {
        position: absolute;
        top: 57.5%;
        left: 25%;
        color: yellow;
        font-family: "LT Colored Pencil", sans-serif;
        font-size: 4vw;
    }
    .home {
        height: 100vh;
        .title {
            font-size: 4vw;
            position: absolute;
            left: 50%;
            top: 52%;
            transform: translate(-50%, -50%);
            color: white;
            font-family: $fontNumber;
            text-transform: uppercase;
            width: max-content;
            font-weight: bold;
            /* opacity: 0; */
        }

        .welcom {
            color: white;
            font-family: $fontNumber;
            text-transform: uppercase;
            left: 50%;
            top: 80%;
            transform: translate(-50%, -50%);
            position: absolute;
            font-weight: bold;
            font-size: 1vw;
            /* opacity: 0; */
        }

        .arrow-home {
            position: absolute;
            left: 50%;
            top: 87%;
            transform: translate(-50%, -50%) rotate(0);
            cursor: pointer;
            animation: arrow-rotation 10s infinite linear;
            &:hover {
                animation: none;
            }
        }

        @keyframes arrow-rotation {
            from {
                transform: translate(-50%, -50%) rotate(0deg);
            }

            to {
                transform: translate(-50%, -50%) rotate(360deg);
            }
        }
    }
    .separation {
        margin-top: calc(4.5vh / 2);
    }

    main {
        z-index: 100;
        margin: 8vw;

        .hoz {
            width: calc(175vw / 2);
            margin-left: calc(5vw / 2);
            margin-top: 1vw;
            position: absolute;
        }

        .ver {
            position: absolute;
            transform: rotate(90deg) translateX(4.5vw);
            width: 25vw;
            left: calc(-9.5vw / 2);
        }

        section {
            margin-top: 14.3rem;
            margin-left: 0.2rem;
            position: relative;
        }

        .container-number {
            display: flex;
            align-items: center;
            width: 100%;
            border-bottom: 1px solid white;
            padding-bottom: 2vw;
            gap: 7vw;
            margin-top: calc(33vw / 2);
        }

        .title-section {
            color: white;
            font-size: calc(4vw);
            font-family: $fontName;
        }

        .description-section {
            color: white;
            font-size: 2.5vw;
            font-family: $fontText;
            margin: calc(7.5vw / 2);
            margin-left: calc(25vw / 2);
        }

        .section-project {
            margin-top: calc(26.5vw / 2);
            .container-number {
                margin-top: 0;
            }
            .ver {
                width: calc(15.75vw / 2);
                transform: rotate(90deg) translateX(-4vw);
                left: calc(7.75vw / 2);
            }
        }

        .container-projects {
            position: relative;
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            margin-left: calc(15.5vw / 2);
            margin-top: 8vw;

            .NavLink {
                text-decoration: none;
                margin-top: 10vw;
                &:first-child {
                    margin-top: 0;
                }
            }

            .guide-line {
                z-index: -1;
                position: absolute;
                left: 0;
                width: 100vw;
                height: 150vw;
            }

            .project {
                .project-img {
                    width: 35vw;
                    border: 1px solid white;
                }

                .project-text {
                    display: flex;
                    align-items: flex-start;
                    flex-direction: column;
                    margin-top: 1.5vw;

                    .project-title {
                        font-size: 3vw;
                        color: white;
                        font-family: $fontText;
                        font-weight: bold;
                    }

                    .project-description {
                        color: $secondColor;
                        font-family: $fontText;
                        font-weight: bold;
                    }
                }
            }
        }
    }
}

.Nav {
    font-family: "Visuelt Pro", sans-serif;
    .container-choices-lang {
        display: flex;
        align-items: center;

        a {
            color: white;
            text-decoration: none;
            font-size: calc(2vw / 2);
        }
    }
    display: flex;
    align-items: center;
    position: absolute;
    width: -webkit-fill-available;
    justify-content: space-between;
    margin-top: calc(11vw / 2);
    margin-left: calc(17vw / 2);
    margin-right: calc(17vw / 2);
    color: white;
    text-transform: uppercase;
    p {
        cursor: pointer;
        font-size: calc(2vw / 2);
    }
    .about-link {
        color: white;
        text-transform: lowercase;
        text-decoration: none;
    }
}
