.container-about {
  display: flex;
  flex-direction: column;
  section {
    margin: 5vh auto;
    margin-bottom: 10vh;
    color: #fff;
    width: fit-content;
  }

  .title-section {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 2vh;
    border-bottom: 2px solid white;
    color: white;
    width: fit-content;
    text-transform: uppercase;
  }

  .description-section {
    font-size: 1.5rem;
    width: 50vw;
    text-transform: lowercase;
  }

  .skills li {
    font-size: 1.5rem;
    text-transform: lowercase;
  }

  .rows-section {
    display: flex;
    align-items: center;
  }

  .skills {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .skills .description-section {
    margin-bottom: 20px;
  }
  .link-about {
    color: white;
    border-bottom-color: white;
  }
}
